.carousel {
	margin-top: 40px;
}

.details {
	display: flex;
  	justify-content: space-between;
	align-items: baseline;
	margin-top: 30px;

	&_infos1 {
		display: flex;
		flex-direction: column;
		gap: 1rem;

		&_title {
			color: var(--color-primary);
			font-size: 36px;
			font-weight: 500;
			line-height: 142.6%;
		}

		&_location {
			color: var(--color-primary);
			font-size: 18px;
			font-weight: 500;
			line-height: 142.6%;
		}

		&_tags {
			display: flex;
			align-items: center;
			justify-content: flex-start;
			gap: 10px;
			margin-top: 20px;
			flex-wrap: wrap;

			& .tag-style {
				display: flex;
				background: var(--color-primary);
				color: var(--white);
				font-family: Montserrat;
				font-size: 18px;
				font-style: normal;
				font-weight: 500;
				line-height: 142.6%;
				border-radius: 10px;
				padding: 1px 15px;
			}
		}
	}

	&_infos2 {
		display: flex;
		flex-direction: column;
		gap: 1rem;

		&_host {
			display: flex;
			align-items: center;
			justify-content: flex-end;
			gap: 10px;

			&_name {
				display: flex;
				flex-direction: column;
				color: var(--color-primary);
				text-align: right;
				font-size: 18px;
				font-weight: 500;
			}

			&_picture {
				width: 65px;
				height: 65px;
				border-radius: 50%;
			}
		}
	}
}

.rating {
	display: flex;
	align-items: center;
	justify-content: flex-end;
}

.logement_collapse {
	display: flex;
	justify-content: space-between;
	gap: 76px;
	margin-top: 24px;
	margin-bottom: 40px;
}

.collapse {
	flex: 1;
}

@media screen and (max-width: 900px) {
	.details {
		display: flex;
		flex-direction: column;
		margin: 0;
	}

	.details_infos2 {
		display: flex;
		flex-direction: row-reverse;
		justify-content: space-between;
		margin-top: 24px;
		width: 100%;
	}


	.logement_collapse {
	display: flex;
	flex-direction: column;
	gap: 20px;
	margin-top: 24px;
	margin-bottom: 40px;
	}
}

@media screen and (max-width: 550px) {
	.details_infos1_title {
	  font-size: 18px;
	}

	.details_infos1_location {
		font-size: 14px;
  }

  .details_infos1_tags .tag-style {
	font-size: 10px;
	line-height: 18px;
  }

  .details_infos2_host_name {
	font-size: 12px;
  }

  .details_infos2_host_picture {
	width: 32px;
	height: 32px;
  }

  .details_infos2 {
	margin-top: 24px;
	width: 100%;
}
}